import React, { useState } from 'react';
import Chat from './components/Chat';
import './styles/App.scss';
import { IconArrowRight } from '@tabler/icons-react';

function App() {
  const [processingSeconds, setProcessingSeconds] = useState(null);
  const [credits, setCredits] = useState(600);
  
  return <div className='main'>
    <div className='text-description'>
      <div>
        <h1>Chat credits </h1>
        <h2>Evoluo para o consumo de crditos e os modos de operao.</h2>
        <div className='point'><IconArrowRight /><p>Utilize o menu dropdown para trocar entre o modo Agente e o modo Dvidas</p></div>
        <div className='point'><IconArrowRight /><p>Clique no boto abaixo para simular 0 crditos e exibir o comportamento esperado.</p></div>
        <div style={{ marginTop: 12 }}>
          <button
            type="button"
            onClick={() => setCredits(prev => prev === 0 ? 600 : 0)}
            style={{
              padding: '6px 10px',
              borderRadius: 6,
              border: '1px solid #ccc',
              background: credits === 0 ? '#e8f5ff' : '#ffe8e8',
              color: '#333',
              cursor: 'pointer',
              fontWeight: 600,
              fontSize: 12
            }}
          >
            {credits === 0 ? 'Simular 600 crditos' : 'Simular 0 crditos'}
          </button>
          
        </div>
      </div>
    </div>
    <div className='content'><Chat credits={credits} onProcessingStart={() => {}} onProcessingEnd={(secs) => setProcessingSeconds(secs)} /></div>
  </div>;
}

export default App;
